body {
    margin: 0;
    font-family: Arial, Helvetica, sans-serif;

}

header .navigation {

    background-color: #1a191f;

    position: fixed;
    color: #ffffff;
    top: 0;
    height: 67px;
    width: 100%;
    opacity: 0.9;

}

.Logo  {
    padding:2px;
    float: left;
   

}
.Logo img{
    padding: 10px 15px;
    height: 30px;
}

.banner-container img {

    position: relative;
    margin: 0;
}



.btn-sign-in a {
    background-color: #a115ad;
    background-image: linear-gradient(to bottom right, #ffb8b7, #ff5da9, #a115ad);

    color: white;
    margin-right: 36px;
    align-content: center;
    padding: 14px 25px;
    margin-top: 10px;
    text-align: center;
    text-decoration: none;
    float: right;
    border-radius: 25px;


}

.btn-sign-in a:hover,
a:active {
    background-color: transparent;
    color: red;
    border-style: solid;
    border-color: red;
    border-width: thin;


}

.banner-heading {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    font-size: 30px;
    font-weight: bold;
    color: #ffffff;
    text-shadow: 2px 2px 5px blue;
}

.btn-container .btn {
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    background-color: #13ffff;
    background-image: linear-gradient(to bottom right, #13ffff, #3ba3ff, #9c00ff);
    color: #ffffff;
    font-size: 16px;
    padding: 10px 45px;
    cursor: pointer;
    border-style: solid;
    border-width: 1px;

}

.btn-container .btn:hover {

    color: #000000;
    height: auto;
    border: none;
}


.about-container h3  {
    text-align: center;
    padding: 3px;
    font-weight: bold;
    color:black;
    font-size: 2em;
}

.about-container .column {
    float: left;
    width: 33.33%;
    padding: 6px;

}
.about-container .column p{
    padding: 3px;
    font-family: Arial, Helvetica, sans-serif;
    text-align: center;
    
}

.about-container .row{
    display: flex;
    flex-wrap: wrap;
    justify-content:space-around;
    
}
.about-container h3{
    text-align: center;
}

.about-container .column{

    font-family: Arial, Helvetica, sans-serif;
    
}


.working-container {

    margin: auto;
    text-align: center;
}
.working-container h3, .working-container p{
    margin: 1em;
}


 .step1 {
    display: flex;
    
    justify-content:space-around;
    align-items: center;
}
.flex-step1 img {
    max-width: 80vh;
}


.latest h3{
    margin: 1em;
    text-align: center;
}
.news-row{
    display: flex;
    margin: 1em;
}
.news-card img {
    max-width: 50vh;
}
.news-column {
  float: left;
  width: 33.3%;
  margin-bottom: 16px;
  padding: 0 8px;
}
.news-card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}
.news-container {
  padding: 4px 16px;
}
.news-title {
  color: grey;
  padding: 3px;
}

.news-button {
  border: none;
  outline: 0;
  display: inline-block;
  padding: 8px;
  color: white;
  margin-top: 5px;
  background-color: #000;
  text-align: center;
  cursor: pointer;
  width: 100%;
}

.news-button:hover {
  background-color: #555;
}

.footer {
    position: relative;
    height: 11em;
    width: 100%;
    background-color: #1a191f;
    opacity: 0.9;
    
  
}
.footer .footer-row{
    display: flex;
   
    
}
.footer-column{
    float: left;
  width: 33.3%;
  padding: 35px;
  color: white;
  align-content: center;
  font-size: 1em;
  

}
.footer .footer-row .footer-column a{
    text-decoration: none;
    color: white;
    margin: 3em;
    
}
.footer-column p{
  
    padding: 2px;
}
.footer-row2{
    text-align: center;
    color: white;
    font-size:1.5em;
}

/* Login Design */

.Login-main-container {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
.Login-svg svg {
    width: 50vh;
    background-color: #f2f2f2;
}
.Login-form {
    /* background-color: #F50057; */
    /* background-image: linear-gradient(to bottom right, #ffb8b7, #ff5da9, #F50057); */
    opacity: 0.7;
    border: 2px solid black;
    

}
.Login-form, .Login-form-group {
    display: flex;
    padding: 5em;
}
.Login-form h1 {
    color: red;
}
.Login-form-group {
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 1.5em;
}
.Login-form-group label {
    padding: 1em;
}
.Login-form-group input {
    padding: 1em;
    border: none;
    border-bottom: 5px solid black;
    /* border-radius: 4em; */
}
.Login-form-group input:focus {
    border-bottom: 5px solid red;

}
.Login-form-group input[type=submit] {
    padding: 1em 5em;
    border-bottom: none;
    background-color: #F50057;
    background-image: linear-gradient(to bottom right, #ffb8b7, #ff5da9, #F50057);
    
}

